@import url(https://fonts.googleapis.com/css2?family=Play:wght@400;700&display=swap);
:root {
  --color-turquoise: #00D4FF;
  --color-coral: #ed7947;
  --color-dimgray: #4c5d67;
  --color-darkslategray: #37474f;
  --color-black: #0b1f34;
  --color-green: #83c045;  
  --color-white: #fff;
  --color-pink: #d64161;
  --color-lightGreen: #b4fa72;
  --color-mediumBlue: #2c6ee7;
  --color-lightBlue: #37507e;
}

/* #86af49*/

body, html {
  padding: 0;
  margin: 0;
}

* {
  font-family: 'Play', sans-serif;
}

body {
  background: #fff;
  background: var(--color-white);
}

a {
  text-decoration: none;
}

.text-primary {
  color: #00D4FF;
  color: var(--color-turquoise);
}

.text-secondary {
  color: #ed7947;
  color: var(--color-coral);
}

button {
  border: none;
  cursor: pointer;
}

button:focus {
  outline: none;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main-header {
    height: 70px;
    padding: 0px 40px;
    display: flex;
    background-color: var(--color-green);
  }
  
  .logo-text {
    padding: 20px;
    font-weight: bold;
    font-size: 24px;
    margin-left: 15px;
    padding-right: 15px;
    color: var(--color-black);
  }
  
  
  .logo-text-2 {
    color: #FFF;
  }

  .logo-image{
    height: 70px;
    width: 70px;
  }
  
  @media (max-width: 850px) {
    .main-header {
      justify-content: center;
    }
  }
.home-container {
    margin-top: 50px ;
    display: flex;
    justify-content: center;
  }
  
  .home-text {
    width: 490px;
    margin-right: 20px;
  }
  
  .home-text-title {
    font-size: 55px;
    color: var(--color-turquoise);
  }
  
  .home-text-subtitle {
    font-size: 24px;
    color: var(--color-darkslategray);
  }
  
  .home-actions {
    display: flex;
    height: 70px;
    margin-top: 50px;
  }
  
  .home-btn {
    background-color: var(--color-lightGreen);
    border-radius: 10px 0px 0px 10px;
    color: var(--color-black);
    font-weight: bold;
    font-size: 24px;
    padding: 20px 45px;
  }
  
  .home-btn:hover {
    background-color: #04c7ef;
  }
  
  .home-btn-icon {
    border-radius: 0px 10px 10px 0px;
    width: 70px;
    background-color: var(--color-green);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  /* responsive */
  
  @media (max-width: 1250px) {
    .home-image {
      max-width: 400px;
      height: auto;
    }
  }
  
  @media (max-width: 1250px) {
    .home-image {
      max-width: 400px;
      height: auto;
    }
  
    .home-text-title {
      font-size: 45px;
    }
    
    .home-text-subtitle {
      font-size: 20px;
    }
  
    .home-text {
      width: 410px;
      margin-right: 20px;
    }
  
    .home-btn {
      font-size: 18px;
      padding: 10px 25px;
    }
  
    .home-actions {
      margin-top: 50px;
    }
  }
  
  @media (max-width: 850px) {
    .home-container {
      margin-top: 50px;
    }
  
    .home-actions {
      height: 50px;
      justify-content: center;
    }
  
    .home-image {
      display: none;
    }
  
    .home-text {
      margin-right: 0;
      text-align: center;
      padding: 0 10px;
    }
  }
.character-table {
    margin-top: 30px;
    width: 100%;
    border-bottom: 10px solid var(--color-turquoise);
    border-radius: 5px 5px 5px 5px;
  }
  
  .character-table thead {
    background-color: var(--color-turquoise);
    text-align: left;
    color: #0E1B28;
    border-radius: 10px;
  }
  
  .character-table thead tr {
    height: 50px;
  }
  
  .character-table thead th:first-child {
    padding-left: 50px;
    border-top-left-radius: 10px;
  }
  
  .character-table thead th:last-child {
    border-top-right-radius: 10px;
  }
  
  .character-table tbody td:first-child {
    padding-left: 25px;
  }
  
  .character-table tbody tr {
    height: 45px;
    color: #FFF;
    font-size: 18px;
  }
  /*Elemento par da lista*/
  .character-table tbody tr:nth-child(odd) {
    background-color: var(--color-darkslategray);
  }
  /*Elemento impar da lista*/
  .character-table tbody tr:nth-child(even) {
    background-color: var(--color-dimgray);
  }
  
  .filters-container {
    background-color: var(--color-darkslategray);
    display: flex;
    justify-content: space-between;
    padding: 13px 15px;
    margin-bottom: 25px;
    border-radius: 10px;
  }
  
  .filters-container input {
    height: 44px;
    font-weight: bold;
    font-size: 16px;
    color: #9E9E9E;
    border: 1px solid #E1E1E1;
    border-radius: 10px;
    box-sizing: border-box;
    margin-right: 20px;
    padding-left: 15px;
  }
  
  .filters-container input:focus {
    outline: none;
  }
  
  .filters-container button {
    font-weight: bold;
    font-size: 16px;
    border-radius: 10px;
    padding: 13px 60px;
  }
  
  .filters-container .clean-filters {
    background-color: var(--color-coral);
    color: #FFF;
    margin-left: 20px;
  }
  
  .filters-container .clean-filters:hover {
    background-color: #e07243;
  }
  
  .filters-container .action-filters {
    background-color: var(--color-turquoise);
    color: #0E1B28;
  }
  
  .filters-container .action-filters:hover {
    background-color: #04c7ef;
  }

  .div-list{
    max-width: 240px;
    font-size: 15px; 
    padding:5px; 
  }

  .character-image{
    height: 100px;
    width: 100px;
  }
.table-title .btn-group {
    float: right;
}
.table-title .btn {
    min-width: 50px;
    border-radius: 2px;
    border: none;
    padding: 6px 12px;
    font-size: 95%;
    outline: none !important;
    height: 30px;
}
.table-title {
    min-width: 100%;
    border-bottom: 1px solid #e9e9e9;
    padding-bottom: 15px;
    margin-bottom: 5px;
    background: rgb(0, 50, 74);
    margin: -20px -31px 10px;
    padding: 15px 30px;
    color: #fff;
}
.table-title h2 {
    margin: 2px 0 0;
    font-size: 24px;
}

.content-input{
    margin-top: 15px;
    margin-left: 10px;
}



.page-container {
    width: 100%;
    margin: 0 auto;
    margin-bottom: 30px;
    padding-top: 5px;
    padding-right: 30px;
    padding-left: 30px;
  }
  
  .filters-container.records-actions {
    justify-content: flex-end;
  }
   
.table-responsive {
  margin: 30px 0;
}
.table-wrapper {
  width: auto;
  background: #fff;
  margin: 0 auto;
  padding: 20px 30px 5px;
  box-shadow: 0 0 1px 0 rgba(0,0,0,.25);
}

